
.sticky{
    position: sticky;
    top: 0;
    z-index: 2;

}
.nav{
   font: optional;
    /* padding: 40px 50px;
    display: flex;
    justify-content: space-around;
    position: fixed;
    width: 100%;
    z-index: 2; */
}
.navlinks>Button{
    font-size: 22px;
   border: none;
   padding: 10px 20px;
   font-weight: bolder;
   font-family: sans-serif;

  
}
.navlinks>Button:hover{
    cursor: pointer;
}


.content h1{
    font-size:80px;
    
  
   margin: 5px 50px;
}

.content>div:nth-child(2){
    display: flex;
    justify-content: center;
    align-items:flex-end;
    font-size: 22px;
}
.availability{
    border: 2px solid white;
    border-radius: 50%;
    height: 300px ;
    width: 300px;

}
.book{
  
    width: 200px;
    position: relative;
    bottom: 180px;
   left:30px
}
.day{
    font-weight: bold;
    font-size: 32px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.introduction{
    position: relative;
    left: 300px;
    margin-top: -70px;
}

.introduction>h2{
    font-size: 42px;
    font-weight: bold;
}
.photo{
    display: flex;
    justify-content: baseline;
    align-items: center;
}
.intro{
    
 
    display: grid;
    justify-content: center;
   margin: 20px auto 60px auto;
    
   
}
.projectcontainer{
    width: 70%;
   
    margin: 10px auto;
}
.project{
    border:1px solid grey;
    padding: 10px ;
margin: 40px 0px;
    display: flex;
    justify-content: space-around;
}

.project>div:nth-child(1){
    flex:7;
}
.project>div>img{
    width: 90%;
    height: 250px;
}
.project>div:nth-child(2){
    flex:3;
    text-align: left;
    font-family: sans-serif;
   margin-top: -10px;
   padding: 5px;
}.project>div:nth-child(2)>a>button{
    background-color: grey;
    margin:  5px;
    margin-top: 15px;
    color: white;
    font-weight: bold;
    border-radius: 5px;
    padding: 5px 10px;
}
.project>div:nth-child(2)>a>button:hover{
    cursor: pointer;
    background-color: wheat;
    color: black;
}
.project>div:nth-child(2)>p>button{
    background-color: rgb(61, 57, 57);
    color: white;
    margin: 5px;
    font-weight: bold;
    border-radius: 5px;
    padding: 5px 10px;
}
.project>div:nth-child(2)>p>button:hover{
   cursor:pointer;
   background-color: wheat;
   color: black;
}
