.contact{
   background-color: white;
   font-size: 20px;
    display: flex;
    justify-content: space-around;
    width: 70%;
    margin: 30px auto 50px auto;
    padding: 20px 0px;
}
.contact>div{
    background-color: white;
}
.contact>div:nth-child(2){
   display: flex;
   gap: 20px;
   color: black;
}
.contact>div:nth-child(2)>a {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.contact>div:nth-child(1)>p{
    display: flex;
    justify-content:left;
    align-items: center;
    gap: 10px;
    background-color: white;
    color: black;
}


@media screen and (min-width: 801px) and (max-width:1200px){
    
    .introduction{
        
        left: 0px;
      margin-top: -40px;
    }
    .content h1{
        font-size:50px;
        
      
        margin: 10px 0px 10px 30px;
    }
    .photo{
        display:grid;
       grid-template-columns: repeat(1,1fr);
       gap:20px
    }
    .intro{
       
        display: grid;
        grid-template-columns: repeat(1,1fr);
        
       padding: 10px 50px;
        
       
    }
    .projectcontainer{
        width: 90%;
       
       
    }
    .contact{
   
    
     
         justify-content: space-between;
         width: 80%;
        
     }
    
    

}

@media screen and (min-width: 320px) and (max-width:800px){
    .introduction{
    
        left: 0px;
        margin-top: -40px;
    }
    .navlinks>Button{
        font-size: 18px;
        padding: 10px 10px;
    
      
    }
    .content h1{
        font-size:40px;
        
      
        margin: 5px 5px;
    }
    .photo{
        display:grid;
       grid-template-columns: repeat(1,1fr);
       gap:10px
    }
    .intro{
       
        display: grid;
        grid-template-columns: repeat(1,1fr);
        
       padding: 10px 50px;
        
       
    }
    .projectcontainer{
        width: 90%;
       
        
    }
    .project{
        
      
        display: grid;
        grid-template-columns: repeat(1,1fr);
    }
    .project>div>img{
        width: 100%;
        
    }
    .contact{
   
       width: 7%;
         display: grid;
        grid-template-columns: repeat(1,1fr);
      
        
     }

     .sticky{
        display: none;
       
    
    }
    .problem{
        font-size: 12px;
    }
   
}